import React from 'react'
import Layout from '../components/Layout'
import { graphql, useStaticQuery } from 'gatsby'


export default function MissieUk() {

   
  return (
    <Layout>
    <div className="genHeading">
          <h1 className="genHeading">MISSION</h1>
          </div>
          <div className="genText">
            <p>
            It is our mission to create a loving society where all forms of life are 
            respected and included. 
            </p>
            <p>
            We do this with trust, acceptance and patience, without expectations, with 
            the light and the good in every human being as our starting point. 
            </p>
          </div>
    </Layout>
)
}
